        //     case 401:
        //         router.push({path: '/login'});
        //         break;
        //     // 403 token                
        //     //                 
        //     // tokenvuextoken                
        //     //                 
        //     case 403:
            
        //     Message.error({
        //         content:'',
        //         duration:3000
        //     })
        //         // token                    
        //         localStorage.removeItem('token');
        //         // store.commit('loginSuccess', null);
        //         // fullPath
        //         setTimeout(() => {
        //             router.push('/login')
        //         }, 1000);
        //         break;
        //     // 404                
        //     case 404:
        //         Message.error({
        //             content:'',
        //             duration:3000
